* {
  margin: 0;
}

body {
  background-color: rgb(234, 237, 237);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    height: 60px;
    display: flex;  
    align-items: center;
    background-color: #131921;
    position: -webkit-sticky;
    position: sticky; 
    top: 0;
    z-index: 100;
}
.header__logo {
    width: 100px;
    object-fit: fill;
    margin: 0 20px;
    margin-top: 18px;
}
.header__searchIcon {
    padding: 4px;
    height: 18px;
    background-color: #d29545;
}
.header__input {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}
.header__search {
    display: flex;
    flex: 1 1;
    align-items: center;
    border-radius: 24px;
}
.header__nav {
    display: flex;
    justify-content: space-evenly;
}
.header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 7px;
    color: white;
    text-align: start;
}
.header__optionLineOne {
    font-size: 10px;
}
.header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}
.header__optionBasket {
    display: flex;
    color: white;
    margin-top: 7px;
    justify-content: space-evenly;
}
.header__basketCount {
    margin-left: 10px;
    margin-right: 10px;
}
.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}
.home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    -webkit-mask-image: linear-gradient(to bottom, rgba(0,0,0,1),rgba(0,0,0,0));
            mask-image: linear-gradient(to bottom, rgba(0,0,0,1),rgba(0,0,0,0));
}

.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
}
.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product__price {
    margin-top: 5px;
    
}
.product__price>small{
    margin-right: 2px;
}
.product__info {
    height: 100px;
    margin-bottom: 15px;
}
.product > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}
.product__rating{
    display: flex;
}
.checkout{
    display: flex;
    padding: 20px;
    background-color: white;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.checkout__ad {
    width: 100%;
    margin-bottom: 10px;
}
.checkout__title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}
